.footer {
    margin-top: auto;
    padding-top: 30px;
    padding-bottom: 28px;
    
  }
  
  .footer-wrapper {
    text-align: center;
  }
  
  .footer-wrapper__text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 2;
    
  }
  
  .footer-wrapper__svg {
    margin: 0px 10px;
  }
  
  .footer-wrapper__underline {
    text-decoration-line: underline;
    cursor: pointer;
  }