.movies {
    margin: 0 auto;
  }
  
  
  
  @media screen and (max-width: 767px) {
    .movies__item:not(:last-child) {
      margin-bottom: 20px;
    }
    .movies {
      width: 280px;
    }
  
    .moviest__img {
      width: 280px;
    }
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    .movies-container {
      width: 618px;
      margin: 0 auto;
    }
    .movies {
      display: flex;
      flex-wrap: wrap;
    }
  
    .movies__item {
      width: 293px;
      margin-bottom: 30px;
  
      &:nth-child(even) {
        margin-left: 30px;
      }
    }
    .movies__item:not(:last-child) {
      margin-bottom: 30px;
    }
  }
  @media screen and (min-width: 1200px) {
    .movies-container {
      width: 882px;
      margin: 0 auto;
    }
  
    .movies {
      display: flex;
      flex-wrap: wrap;
    }
    .movies__item {
      width: 274px;
      margin-right: 30px;
      margin-bottom: 30px;
  
      &:nth-child(3n) {
        margin-right: 0px;
      }
      &:nth-last-child(-n + 3) {
        margin-bottom: 0;
      }
    }
  }
  